import React, { useState } from 'react';

import './App.css';

import arrow from './assets/nextarrow.png';
import Panel1 from './panels/Panel1/Panel1';
import Panel2 from './panels/Panel2/Panel2';
import Panel3 from './panels/Panel3/Panel3';
import Panel4 from './panels/Panel4/Panel4';
import Panel5 from './panels/Panel5/Panel5';
import Panel6 from './panels/Panel6/Panel6';
import Panel7 from './panels/Panel7/Panel7';
import Panel8 from './panels/Panel8/Panel8';
import Panel9 from './panels/Panel9/Panel9';
import Panel10 from './panels/Panel10/Panel10';
import Panel11 from './panels/Panel11/Panel11';

function App() {
	const [pageNumber, setPageNumber] = useState(11);

	const handleNextPageChange = () => {
		if (pageNumber < 11) {
			setPageNumber(pageNumber + 1);
		} else {
			setPageNumber(1);
		}
	};

	const handlePrevPageChange = () => {
		if (pageNumber > 1) {
			setPageNumber(pageNumber - 1);
		}
	};

	return (
		<div className="wrapper">
			<div
				className="prev-arrow-wrapper"
				onClick={() => handlePrevPageChange()}
			>
				<img src={arrow} className="prev-arrow-image" alt="arrow" />
			</div>
			<div className="content-wrapper">
				{pageNumber === 1 && <Panel1 />}
				{pageNumber === 2 && <Panel2 />}
				{pageNumber === 3 && <Panel3 />}
				{pageNumber === 4 && <Panel4 />}
				{pageNumber === 5 && <Panel5 />}
				{pageNumber === 6 && <Panel6 />}
				{pageNumber === 7 && <Panel7 />}
				{pageNumber === 8 && <Panel8 />}
				{pageNumber === 9 && <Panel9 />}
				{pageNumber === 10 && <Panel10 />}
				{pageNumber === 11 && <Panel11 />}
			</div>
			<div
				className="next-arrow-wrapper"
				onClick={() => handleNextPageChange()}
			>
				<img src={arrow} className="next-arrow-image" alt="arrow" />
			</div>
		</div>
	);
}

export default App;
